@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Montserrat';
  background: linear-gradient(to right, #16222a, #3a6073);
  color: white;
}

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

a {
  color: white;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

.movies-container {
  margin-top: 15px;

  display: flex;
  justify-content: center;
  grid-gap: 15px;
  gap: 15px;
  flex-wrap: wrap;
}

.movie {
  cursor: pointer;
  transition: ease 0.2s;
  border-radius: 6px;
  box-shadow: 0 2px 5px -1px rgb(0 0 0 / 60%);
}

.movie:hover {
  transform: scale(1.05);
}

.arrows-container {
  margin-top: auto;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  padding: 50px 0;
}

.nav-button {
  border: none;
  outline: none;
  padding: 15px 40px;
  cursor: pointer;
  transition: ease 0.2s;
  color: white;
  font-family: 'Montserrat';
}

.nav-button:hover {
  transform: scale(1.05);
}

.non-available {
  background: #34495e;
}

.available {
  background: #2ecc71;
}

@media screen and (max-width: 600px) {
  .arrows-container {
    grid-gap: 15px;
    gap: 15px;
  }
}

.search-movies-form {
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 15px;
  gap: 15px;
  background: rgb(0 0 0 / 15%);
  width: 100%;
  box-shadow: 0 -1px 30px -1px rgb(0 0 0 / 60%);
  padding: 20px 0;
}

.movies-searchbar,
.submit-button {
  border: none;
  outline: none;
  padding: 15px;
  color: white;
  font-family: 'Montserrat';
}

.movies-searchbar {
  background: rgb(0 0 0 / 40%);
}

.submit-button {
  cursor: pointer;
  transition: ease 0.2s;
  background: rgb(0 0 0 / 60%);
}

.submit-button:hover {
  transform: scale(1.05);
}

